"use client";
import React from "react";
import type { VendaFinal } from "@/services/api/venda.service";
import VendaFinalCard from "@/components/vendas/VendaFinalCard";

type Props = {
  items: VendaFinal[];
  onSend?: (id: number) => void;
};

export function RecentInvoices({ items, onSend }: Props) {
  if (!items?.length) {
    return (
      <div className="rounded-2xl border border-dashed border-gray-200 p-6 text-center text-gray-500 dark:border-gray-800 dark:text-gray-400">
        Sem faturas recentes
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {items.map((vf) => (
        <VendaFinalCard
          key={vf.id}
          vendaFinal={vf}
          onSend={onSend ? () => onSend(vf.id) : undefined}
          onEdit={() => {}}
          onDelete={() => {}}
          showActions={false}
        />)
      )}
    </div>
  );
}
