"use client";
import { useMemo } from "react";
import { formatCurrency } from "@/utils/format";
import { ApexOptions } from "apexcharts";
import dynamic from "next/dynamic";

const ReactApexChart = dynamic(() => import("react-apexcharts"), {
  ssr: false,
});

const DEFAULT_MONTH_LABELS = [
  "Jan",
  "Fev",
  "Mar",
  "Abr",
  "Mai",
  "Jun",
  "Jul",
  "Ago",
  "Set",
  "Out",
  "Nov",
  "Dez",
];

type RevenueItem = { label: string; revenue: number };

type RevenueByMonthChartProps = {
  data: Array<RevenueItem | number>;
};

export function RevenueByMonthChart({ data }: RevenueByMonthChartProps) {
  const normalizedData: RevenueItem[] = useMemo(() => {
    if (!data || data.length === 0) {
      return DEFAULT_MONTH_LABELS.map((label) => ({ label, revenue: 0 }));
    }

    const first = data[0];

    if (typeof first === "number") {
      const numericData = data as number[];

      return DEFAULT_MONTH_LABELS.map((label, index) => ({
        label,
        revenue: Number(numericData[index] ?? 0),
      }));
    }

    return (data as RevenueItem[]).map((item, index) => ({
      label: item.label ?? DEFAULT_MONTH_LABELS[index] ?? `M${index + 1}`,
      revenue: Number(item.revenue ?? 0),
    }));
  }, [data]);

  const categories = normalizedData.map((item) => item.label);
  const seriesValues = normalizedData.map((item) => item.revenue);

  const options: ApexOptions = {
    colors: ["#465fff"],
    chart: {
      fontFamily: "Outfit, sans-serif",
      type: "bar",
      height: 200,
      toolbar: { show: false },
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: "39%",
        borderRadius: 5,
        borderRadiusApplication: "end",
      },
    },
    dataLabels: { enabled: false },
    stroke: { show: true, width: 4, colors: ["transparent"] },
    xaxis: {
      categories,
      axisBorder: { show: false },
      axisTicks: { show: false },
    },
    legend: { show: false },
    yaxis: { labels: { formatter: (val) => `${formatCurrency(val)}` } },
    grid: { yaxis: { lines: { show: true } } },
    fill: { opacity: 1 },
    tooltip: {
      x: { show: false },
      y: { formatter: (val: number) => `${formatCurrency(val)}` },
    },
  };

  const series = [
    {
      name: "Receita",
      data: seriesValues,
    },
  ];

  return (
    <div className="overflow-hidden rounded-2xl border border-gray-200 bg-white px-5 pt-5 dark:border-gray-800 dark:bg-white/[0.03] sm:px-6 sm:pt-6">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">
          Receita por mês
        </h3>
      </div>
      <div className="max-w-full overflow-x-auto custom-scrollbar">
        <div className="-ml-5 min-w-[650px] xl:min-w-full pl-2">
          <ReactApexChart options={options} series={series} type="bar" height={200} />
        </div>
      </div>
    </div>
  );
}