"use client";
import { ApexOptions } from "apexcharts";
import dynamic from "next/dynamic";
import React from "react";

const ReactApexChart = dynamic(() => import("react-apexcharts"), {
  ssr: false,
});

type Item = { status: string; total: number };

const translateLabels: { [key: string]: string } = {
  PENDING: "Pendente",
  APPROVED: "Pago",
  CANCELLED: "Cancelado",
  PENDENTE: "Pendente",
  PAGO: "Pago",
};
const corByStatus: { [key: string]: string } = {
  PENDING: "#f59e0b",   // Amarelo
  APPROVED: "#10b981",  // Verde
  CANCELLED: "#ef4444", // Vermelho
  PENDENTE: "#f59e0b",
  PAGO: "#10b981",
};

export function SalesByStatusChart({ items }: { items: Item[] }) {
  const categories = items?.map((i) => translateLabels[i.status] ?? i.status) ?? [];
  const seriesData = items?.map((i) => i.total) ?? [];
  const colors = items?.map((i) => corByStatus[i.status] ?? "#6366f1");

  const options: ApexOptions = {
    chart: { type: "donut", height: 320, fontFamily: "Outfit, sans-serif" },
    labels: categories,
    legend: { position: "bottom" },
    dataLabels: { enabled: true },
    colors,
    tooltip: {
      y: {
        formatter: (val: number) => `${val}`,
      },
    },
  };

  return (
    <div className="rounded-2xl border border-gray-200 bg-white px-5 pt-5 dark:border-gray-800 dark:bg-white/[0.03] sm:px-6 sm:pt-6">
      <h3 className="mb-4 text-lg font-semibold text-gray-800 dark:text-white/90">
        Vendas por status
      </h3>
      <ReactApexChart options={options} series={seriesData} type="donut" height={320} />
    </div>
  );
}
