"use client";
import React, { useEffect, useMemo, useState } from "react";
import { useAuth } from "@/hooks/useAuth";
import { DashboardKpis } from "@/components/dashboard/DashboardKpis";
import { RevenueByMonthChart } from "@/components/dashboard/RevenueByMonthChart";
import { SalesByStatusChart } from "@/components/dashboard/SalesByStatusChart";
import { vendaFinalService, type VendaFinal, TipoPagamento, StatusVendaFinal } from "@/services/api/venda.service";
import { RecentInvoices } from "@/components/dashboard/RecentInvoices";
import Select from "@/components/ui/Select";
import Input from "@/components/form/input/InputField";
import DatePicker from "@/components/form/date-picker";
import Pagination from "@/components/ui/Pagination";
import { gatewayService, type Gateway } from "@/services/api/gateway.service";
import { clienteService, type ClienteList } from "@/services/api";
import { set } from "react-hook-form";

type OrderBy = 'createdAt_desc' | 'createdAt_asc' | 'vencimento_desc' | 'vencimento_asc' | 'valor_desc' | 'valor_asc';


export default function EmpresaDashboardPage() {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [recent, setRecent] = useState<VendaFinal[]>([]);
  const [gateways, setGateways] = useState<Gateway[]>([]);
  const [clientes, setClientes] = useState<ClienteList>({ data: [], pagination: { total: 0, page: 1, limit: 1, pages: 0 }, message: '' });
  // pegar a data de inicio do ano e de fim do ano atual
  const startOfYear = new Date(new Date().getFullYear(), 0, 1);
  const endOfYear = new Date(new Date().getFullYear(), 11, 31);
  // filtros
  const [search, setSearch] = useState("");
  const [type, setType] = useState<TipoPagamento | undefined>(undefined);
  const [status, setStatus] = useState<StatusVendaFinal | undefined>(undefined);
  const [vencimento, setVencimento] = useState<string | undefined>(`${startOfYear.toISOString()},${endOfYear.toISOString()}`);
  const [gatewayId, setGatewayId] = useState<number | undefined>(undefined);
  const [orderBy, setOrderBy] = useState<OrderBy>("createdAt_desc");
  const [page, setPage] = useState(1);
  const [limit, setLimit] = useState(10);
  const [totalPages, setTotalPages] = useState(0);
  const empresaId = user?.empresaId;

  useEffect(() => {
    let cancelled = false;
    const fetchData = async () => {
      if (!empresaId) return;
      try {
        setLoading(true);
        // carregar gateways (para filtro) e vendas-finais (para KPIs, gráficos e recentes)
        const [gwRes, vfRes, clientesRes] = await Promise.all([
          gatewayService.list({ page: 0, limit: 0 }),
          vendaFinalService.list({ page, limit, search, type, status, vencimento, gatewayId, orderBy }),
          clienteService.list({
            page: 1,
            limit: 1,
            search: ''
          })
        ]);
        if (cancelled) return;
        setGateways(gwRes.data || []);
        setRecent(vfRes.data || []);
        setClientes(clientesRes);
        setTotalPages(vfRes.pagination?.pages || 0);
      } catch (err) {
        console.error("Erro ao carregar dashboard", err);
      } finally {
        if (!cancelled) setLoading(false);
      }
    };
    fetchData();
    return () => {
      cancelled = true;
    };
  }, [empresaId, page, limit, search, type, status, vencimento, gatewayId, orderBy]);

  // Receita mensal calculada a partir da lista de vendas-finais do ano atual
  const receitaMensal = useMemo(() => {
    const arr = new Array(12).fill(0);
    if (!recent?.length) return arr;
    const currentYear = new Date().getFullYear();
    for (const v of recent) {
      const dt = new Date(v.vencimento);
      if (v.status !== 'APPROVED') continue;
      if (dt.getFullYear() !== currentYear) continue;
      const month = dt.getMonth(); // 0..11
      const valor = Number(v.valor ?? 0);
      if (!Number.isNaN(valor)) arr[month] += valor;
    }
    console.log('receitaMensal:', arr);
    return arr;
  }, [recent]);

  // Vendas por status somando valores por status na lista carregada
  const vendasPorStatus = useMemo(() => {
    const map = new Map<string, number>();
    for (const v of recent) {
      const key = v.status;
      const prev = map.get(key) || 0;
      map.set(key, prev + (Number(v.valor) || 0));
    }
    return Array.from(map.entries()).map(([status, total]) => ({ status, total }));
  }, [recent]);

  const handleSend = async (id: number) => {
    try {
      await vendaFinalService.send(id);
    } catch (e) {
      // silencioso no dashboard; telas próprias possuem toast
    }
  };

  return (
    <div className="grid grid-cols-12 gap-4 md:gap-6">
      <div className="col-span-12 space-y-6 xl:col-span-7">
        <DashboardKpis
          totalClientes={clientes.pagination.total}
          totalVendas={recent.length}
          totalRecebido={recent.filter(v => v.status === 'APPROVED').reduce((s, v) => s + (Number(v.valor) || 0), 0)}
        />

        <RevenueByMonthChart data={receitaMensal} />
      </div>

      <div className="col-span-12 xl:col-span-5">
        <SalesByStatusChart items={vendasPorStatus} />
      </div>

      <div className="col-span-12 space-y-4">
        <div className="flex flex-col gap-3 md:flex-row md:items-end md:justify-between">
          <div className="flex-1 max-w-xs">
            <Input
              type="text"
              placeholder="Buscar faturas..."
              value={search}
              onChange={(e) => { setSearch(e.target.value); setPage(1); }}
            />
          </div>
          <div className="flex flex-wrap gap-3 items-end">
            <Select
              placeholder="Tipo"
              options={[{ value: 'PIX', label: 'PIX' }, { value: 'BOLETO', label: 'Boleto' }]}
              value={type ? { value: type, label: type } : undefined}
              onChange={(opt) => { setType(opt ? (opt as any).value as TipoPagamento : undefined); setPage(1); }}
              isClearable
              className="min-w-40"
            />
            <Select
              placeholder="Status"
              options={[{ value: 'PENDING', label: 'Pendente' }, { value: 'APPROVED', label: 'Aprovado' }, { value: 'CANCELLED', label: 'Cancelado' }]}
              value={status ? { value: status, label: status } : undefined}
              onChange={(opt) => { setStatus(opt ? (opt as any).value as StatusVendaFinal : undefined); setPage(1); }}
              isClearable
              className="min-w-40"
            />
            <div className="min-w-48">
              <DatePicker
                id="vencimento"
                mode="range"
                onClickClean={(fp) => { if (fp) { fp.clear(); fp.close(); setVencimento(undefined); setPage(1); } }}
                onClickHj={(fp) => { if (fp) { const hj = new Date(); fp.setDate(hj); fp.close(); setVencimento(hj.toISOString()); setPage(1); } }}
                onClickAno={(fp) => { if (fp) { 
                  const hj = new Date();
                  const inicioAno = new Date(hj.getFullYear(), 0, 1);
                  const fimAno = new Date(hj.getFullYear(), 11, 31);
                  fp.setDate(inicioAno);
                  fp.setDate(fimAno);
                  fp.close();
                  setVencimento(`${inicioAno.toISOString()},${fimAno.toISOString()}`);
                  setPage(1);
                }}}
                onClickMes={(fp) => { if (fp) { 
                  const hj = new Date();
                  const inicioMes = new Date(hj.getFullYear(), hj.getMonth(), 1);
                  const fimMes = new Date(hj.getFullYear(), hj.getMonth() + 1, 0);
                  fp.setDate(inicioMes);
                  fp.setDate(fimMes);
                  fp.close();
                  setVencimento(`${inicioMes.toISOString()},${fimMes.toISOString()}`);
                  setPage(1);
                } }}
                onChange={(dates: Date[]) => {
                  const dateLength = dates.length;
                  const date = dateLength > 1 ? dates?.[0]?.toISOString() + ',' + dates?.[1]?.toISOString() : dates?.[0]?.toISOString();

                  // não define o vencimento até selecionar as 2 datas
                  if (dateLength > 1) {
                    setVencimento(date);
                    setPage(1);
                  }
                }
                }
                defaultDate={vencimento?.split(',').map(d => new Date(d)) || undefined}
              />
            </div>
            <Select
              placeholder="Gateway"
              options={gateways.map(g => ({ value: g.id.toString(), label: `${g.nome} (${g.gateway})${g.isDefault ? ' (Padrão)' : ''}` }))}
              value={gatewayId ? { value: gatewayId.toString(), label: gateways.find(g => g.id === gatewayId)?.nome || '' } : undefined}
              onChange={(opt) => { setGatewayId(opt ? parseInt((opt as any).value) : undefined); setPage(1); }}
              isClearable
              className="min-w-50"
            />
            <Select
              placeholder="Ordenar por"
              options={[
                { value: 'createdAt_desc', label: 'Criado - Mais recente' },
                { value: 'createdAt_asc', label: 'Criado - Mais antigo' },
                { value: 'vencimento_asc', label: 'Vencimento - Mais próximo' },
                { value: 'vencimento_desc', label: 'Vencimento - Mais distante' },
                { value: 'valor_desc', label: 'Valor - Maior' },
                { value: 'valor_asc', label: 'Valor - Menor' },
              ]}
              value={{ value: orderBy, label: '' }}
              onChange={(opt) => { setOrderBy(opt ? (opt as any).value as OrderBy : 'createdAt_desc'); setPage(1); }}
              isClearable={false}
              className="min-w-50"
            />
          </div>
        </div>

        <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">Faturas</h3>
        <RecentInvoices items={recent} onSend={handleSend} />

        <div className="flex items-center justify-between mt-4">
          <div className="text-sm text-gray-500 dark:text-gray-400">
            Itens por página:
            <select
            title="Itens por página"
              className="ml-2 rounded border border-gray-300 bg-white px-2 py-1 text-sm dark:bg-gray-900 dark:border-gray-700"
              value={limit}
              onChange={(e) => { setLimit(parseInt(e.target.value)); setPage(1); }}
            >
              {[5, 10, 20].map(n => <option key={n} value={n}>{n}</option>)}
            </select>
          </div>
          <Pagination
            totalPages={totalPages}
            currentPage={page}
            onPageChange={setPage}
          />
        </div>
      </div>
    </div>
  );
}
